
p.typo {
	padding-top: 20px;
	padding-bottom: 30px;
}
mark {
	background: transparent;
	color: #f55c5d!important;
}
.blockquote {
	position: relative;
	padding: 28px;
	margin-bottom: 20px;
	border: 1px solid $border-color;
	border-left: 3px solid $primary-color;
	font-size: 15px;
	line-height: 28px;
	font-style: italic;
	&.text-right {
		border-right: 3px solid $primary-color;
		border-left: 1px solid $border-color;
	}
}
.blockquote-bubble {
	position: relative;
	.blockquote-bubble-content {
		&:after {
			top: 100%;
			border: 1px solid $border-color;
			content: " ";
			height: 20px;
			width: 20px;
			position: absolute;
			pointer-events: none;
			background:  $white-color;
			left: 30px;
			margin-top: -9px;
			transform: rotate(45deg);
			border-left: 0px;
			border-top: 0px;
		}
	}
	&.text-right {
		.blockquote-bubble-content {
			&:after {
				right: 30px;
				left: auto;
			}
		}
	}
}
.blockquote-footer {
	margin-bottom: 20px;
	position: relative;
	display: flex;
	img {
		width: 60px;
		height: 60px;
		background: $sand;
		float: left;
	}
	.info {
		display: inline-block;
		padding-left: 10px;
		float: left;
		margin-top: 8px;
	}
	.name {
		font-size: 15px;
		color: $title-color;
		display: inline-block;
	}
	.designation {
		font-size: 15px;
		color: $text-color;
		display: inline-block;
	}
	&:before {
	    content: "\2014 \00A0";
	    visibility: hidden;
	}
	&.text-right {
		flex-direction: row-reverse;
		img {
			float: right;
		}
		.info {
			float: right;
			padding-left: 0px;
			padding-right: 10px;
		}
	}
}

// List elements =========================
.styled_list {
	margin-top: 15px;
	position: relative;
	display: inline-block;
	@include tablet {
		margin-top: 15px;
	}
	li {
		font-size: 14px;
		line-height: 30px;
		@include tablet {
			font-size: 14px;
			float: left;
			width: 50%;
		}
		i {
			margin-right: 10px;
			font-size: 12px;
		}
		a {
			color: $text-color;
			@include tablet {
				font-size: 12px;
			}
			@include desktops {
				font-size: 14px;
			}
		}
	}
}
ol.styled_list {
	margin-left: 15px;
	li {
		padding-left: 10px;
	}
}

.icon-sm {
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 22px;
    display: inline-block;
}
.dropcaps {
	p {
		display: inline-block;
		.firstcharacter {
			color: $text-color;
			float: left;
			font-size: 22px;
			width: 40px;
			height: 40px;
			background-color: $sand;
			text-align: center;
			line-height: 40px;
			margin-top: 9px;
			margin-right: 10px;
		}
	}
}