.about_phylosophy {
	padding-top: 80px;
	border-top: 1px solid $border-color;
	.about_phylosophy-image {
	    width: 100%;
	    overflow: hidden;
	    img {
	    	width: 100%;
	    }
	}
	.about_phylosophy-desc {
		padding: 10px;
		h2 {
			margin-bottom: 20px;
		}
		ul.list-group {
		    padding: 15px 0 20px;
		    li.list-group-item {
		        border: none;
		        padding: 0px 0px;
		        font-size: 13px;
		        line-height: 30px;
		        .fa {
		        	color: $primary-color;
		        	font-size: 13px;
		        }
		    }
		}
	}
}
/* ================================================
--> About team style
================================================== */
.team-wrapper {
	padding-top: 80px;
	padding-bottom: 80px;
	@include desktops {
		padding-top: 100px;
		padding-bottom: 100px;
	}
	.sectionTitle {
		h2 {
			padding-bottom: 0px;
		}
	}
	.team-card{
		margin-top: 50px;
		&:hover {
			.hover-overlay {
				@include transform(translateY(0%));
			}
		}
		a.overlay_icon {
			width: 45px;
			height: 45px;
			background-color: transparent;
			border: 2px solid $white-color;
			.fa {
				font-size: 15px;
				line-height: 40px;
				color: $white-color;
			}
			&:hover {
				background-color: $white-color;
				border: 2px solid $white-color;
				.fa {
					color: $primary-color;
				}
			}
		}
	}
}